"use strict";

// Burger menu
var burger = document.querySelector('.js-burger');
var mobileMenu = document.querySelector('.js-mobile-menu');
burger.addEventListener('click', function () {
  burger.classList.toggle('active');
  mobileMenu.classList.toggle('active');
});

// Modal
document.addEventListener('DOMContentLoaded', function () {
  var MODAL_TIMEOUT = 5000;
  var modal = document.getElementById('modal');
  var closeBtn = document.getElementById('modal-close');
  var modalTitle = document.getElementById('modal-title');
  var progressBar = document.getElementById('progress-bar');
  var comingSoonLinks = document.querySelectorAll('[data-soon]');
  var autoCloseTimeout;
  function showModal(title) {
    modalTitle.textContent = title;
    modal.classList.add('show');
    document.body.classList.add('no-scroll');
    progressBar.classList.add('animate');
    clearTimeout(autoCloseTimeout);
    autoCloseTimeout = setTimeout(hideModal, MODAL_TIMEOUT);
  }
  function hideModal() {
    modal.classList.remove('show');
    document.body.classList.remove('no-scroll');
    progressBar.classList.remove('animate');
  }
  comingSoonLinks.forEach(function (link) {
    link.addEventListener('click', function (e) {
      e.preventDefault();
      var title = link.dataset.title || 'This';
      showModal(title);
    });
  });
  closeBtn.addEventListener('click', function () {
    clearTimeout(autoCloseTimeout);
    hideModal();
  });
  modal.addEventListener('click', function (e) {
    if (e.target === modal) {
      clearTimeout(autoCloseTimeout);
      hideModal();
    }
  });
  document.addEventListener('keydown', function (e) {
    if (e.key === 'Escape' && modal.classList.contains('show')) {
      clearTimeout(autoCloseTimeout);
      hideModal();
    }
  });
});